<template>
  <div class="app">
    <ProductList
        v-if="view === 'list'"
        :initialCategory="currentCategory"
        @view-detail="loadDetail"
        @update-category="currentCategory = $event"
    />

    <ProductDetail
        v-else-if="view === 'detail' && detailData"
        :data="detailData"
        @go-back="view = 'list'"
    />
  </div>
</template>

<script setup>
import { ref } from 'vue';
import axios from 'axios';
import ProductList from './components/ProductList.vue';
import ProductDetail from './components/ProductDetail.vue';

const view = ref('list');
const detailData = ref(null);
// 2. 현재 카테고리 상태를 부모에서 관리 (기본값 데스크탑)
const currentCategory = ref('데스크탑');

const loadDetail = async (code) => {
  try {
    const res = await axios.get(`http://localhost:8083/api/product/${code}`);
    detailData.value = res.data;
    view.value = 'detail';
  } catch (error) {
    console.error("상세 정보 로드 실패:", error);
  }
};

// 3. 기존의 onMounted 내부 전체조회 로직은 삭제합니다.
// (ProductList 컴포넌트 내부에서 스스로 데이터를 가져오도록 설계되었기 때문)
</script>